rder-color: #ccc;\n  cursor: not-allowed;\n}\n\n/* custom style for input[type=\"range] (slider) to improve alignment between positions and labels */\n\n.jspsych-slider {\n  appearance: none;\n  -webkit-appearance: none; \n  -moz-appearance: none;\n  width: 100%; \n  background: transparent; \n}\n.jspsych-slider:focus {\n  outline: none;\n}\n/* track */\n.jspsych-slider::-webkit-slider-runnable-track {\n  appearance: none;\n  -webkit-appearance: none;\n  width: 100%;\n  height: 8px;\n  cursor: pointer;\n  background: #eee;\n  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;\n  border-radius: 2px;\n  border: 1px solid #aaa;\n}\n.jspsych-slider::-moz-range-track {\n  appearance: none;\n  width: 100%;\n  height: 8px;\n  cursor: pointer;\n  background: #eee;\n  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;\n  border-radius: 2px;\n  border: 1px solid #aaa;\n}\n.jspsych-slider::-ms-track {\n  appearance: none;\n  width: 99%;\n  height: 14px;\n  cursor: pointer;\n  background: #eee;\n  box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;\n  border-radius: 2px;\n  border: 1px solid #aaa;\n}\n/* thumb */\n.jspsych-slider::-webkit-slider-thumb {\n  border: 1px solid #666;\n  height: 24px;\n  width: 15px;\n  border-radius: 5px;\n  background: #ffffff;\n  cursor: pointer;\n  -webkit-appearance: none;\n  margin-top: -9px;\n}\n.jspsych-slider::-moz-range-thumb {\n  border: 1px solid #666;\n  height: 24px;\n  width: 15px;\n  border-radius: 5px;\n  background: #ffffff;\n  cursor: pointer;\n}\n.jspsych-slider::-ms-thumb {\n  border: 1px solid #666;\n  height: 20px;\n  width: 15px;\n  border-radius: 5px;\n  background: #ffffff;\n  cursor: pointer;\n  margin-top: -2px;\n}\n\n/* jsPsych progress bar */\n\n#jspsych-progressbar-container {\n  color: #555;\n  border-bottom: 1px solid #dedede;\n  background-color: #f9f9f9;\n  margin-bottom: 1em;\n  text-align: center;\n  padding: 8px 0px;\n  width: 100%;\n  line-height: 1em;\n}\n#jspsych-progressbar-container span {\n  font-s